@mixin font($font_name, $file_name, $weight, $style)  {
  @font-face {
     font-family: $font_name;
     font-display: swap;
     font-style: #{$style};
     font-weight: #{$weight};
     src: local(#{$file_name}), url("../fonts/#{$file_name}.woff") format("woff"), url("../fonts/#{$file_name}.woff2") format("woff2");
  }
}

@mixin retina {
 @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
   @content;
 }
}

// Desktop first

@mixin viewport--xl {
  @media (max-width: $viewport--xxl - 0.02) {
    @content;
  }
}

@mixin viewport--lg {
  @media (max-width: $viewport--xl - 0.02) {
    @content;
  }
}

@mixin viewport--md {
  @media (max-width: $viewport--lg - 0.02) {
    @content;
  }
}

@mixin viewport--sm {
  @media (max-width: $viewport--md - 0.02) {
    @content;
  }
}

@mixin viewport--xs {
  @media (max-width: $viewport--sm - 0.02) {
    @content;
  }
}

