// !        ( input, textarea )

/* @media (hover: hover), screen and (min-width: 0\0) {
  &:hover,
  &:focus {
    color: red;
  }
} */

// hard reset

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}


li {
  list-style: none;
}


ul {
  padding-left: 0;
  margin: 0;
}

input[type="number"]:hover,
input[type="number"]:focus {
   -moz-appearance: number-input
}

input {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

button {
  background: none;
  border: none;
}
